* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-size: 15px;
  font-family: sans-serif;
  min-height: 100vh;
}

.Navbar {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 1rem;
  background-color: #dd4a48;
  box-shadow: 0 3px 7px rgba(0, 0, 0, 0.31);

  margin-bottom: 2rem;
}

.Navbar h3 {
  color: #fff;
}

.SearchBar {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  column-gap: 1rem;
  margin-bottom: 1rem;
}

.SearchBar input {
  text-align: center;
  outline: none;
  border: none;
  border-radius: 5px;
  width: 40vw;
  padding: 0.5rem 0.2rem;
  box-shadow: 0 3px 7px rgba(0, 0, 0, 0.17);
}

.SearchBar button {
  padding: 0.5rem 0;
  width: 120px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  color: #fff;
  font-weight: bold;
  font-size: 0.85rem;
  letter-spacing: 1.3px;
  text-transform: uppercase;
  background-color: #dd4a48;
  box-shadow: 0 3px 7px rgba(0, 0, 0, 0.17);
}

.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 2rem;
  margin-bottom: 1rem;
}

.pagination button {
  border: none;
  background: skyblue;
  font-size: 1.2rem;
  cursor: pointer;
}

.pokedex-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(4, 1fr);
  row-gap: 2.5rem;
}


.pokemon-card {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 1rem;
  margin: auto;
  border-radius: 20px;
  width: 350px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.21);
}

.pokemon-name {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-grow: 1;
  flex-basis: 0;
}
.pokemon-name h6 {
  font-size: 1rem;
  font-weight: bold;
  text-transform: capitalize;
}

.pokemon-name p {
  color: #5a5a5a;
  font-size: 0.8rem;
}

.pokemon-type {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 0.6rem;
}

.card-info {
  display: flex;
  flex-direction: column;
  flex: 1;
  gap: 0.7rem;
  padding: 1rem;
}

.footer {
  background-color: #dd4a48;
  margin-top: 3rem;
  padding: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 -3px 10px rgba(0, 0, 0, 0.21);
}

